<template>
  <div
    v-loading="isLoading"
    class="report-list"
  >
    <div class="clearfix">
      <gs-button
        class="pull-right"
        @click="dowmLoadTemplate()"
      >下载</gs-button>
    </div>
    <el-table
      :data="reportData"
      stripe
      @select="selectRowsClick"
      @select-all="selectRowsClick"
    >
      <el-table-column
        type="selection"
        width="55"
      />
      <el-table-column
        label="报告名称"
        align="center"
        prop="reportName"
        show-overflow-tooltip
      />
      <el-table-column
        label="橙色警告"
        align="center"
        prop="orangeAlerts"
        show-overflow-tooltip
      />
      <el-table-column
        label="红色警告"
        align="center"
        prop="redAlerts"
        show-overflow-tooltip
      />
      <el-table-column
        label="生成日期"
        align="center"
        width="220"
        prop="reportProduceDate"
        show-overflow-tooltip
      />
    </el-table>
    <gs-pagination
      v-show="table.total>0"
      :current="table.currentPage"
      :total="table.total"
      :page-size="table.pageSize"
      :page-sizes="table.pageSizes"
      layout="range,total,pagesizes,pager,jumper,jumpbtn"
      @page-change="pageChange"
      @page-size-change="pageSizeChange"
    />
  </div>
</template>

<script>
import './report-list.scss';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  props: {
    templateId: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      isLoading: false,
      selectRows: [],
      table: {
        currentPage: 1,
        total: 20,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40, 50, 100]
      },
      reportData: []
    };
  },

  watch: {
    templateId(val) {
      this.getAllReports();
    }
  },

  beforeMount() {
    this.getAllReports();
  },

  methods: {
    /**
     * 下载模板
     */
    dowmLoadTemplate() {
      if (this.selectRows.length <= 0) {
        this.$Message.warning('请至少选择一个报告');
        return;
      }
      const ids = this.selectRows.map(item => item.reportId);
      dataQualityService.getReportsDownloadUrl(ids).then((res) => {
        this.isLoading = false;
        window.open(res.downloadUri + '?X-SSO-FullticketId=' + this.$store.state.user.userInfo.Ticket);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 选中记录事件
     */
    selectRowsClick(rows) {
      this.selectRows = rows;
    },
    /**
     * 查看报告详情
     */
    getAllReports() {
      this.isLoading = true;
      const paginationParam = this.getPaginationParam();
      dataQualityService.getAllReports(this.templateId, paginationParam.offset, paginationParam.limit).then((res) => {
        this.isLoading = false;
        this.reportData = res.reports;
        this.table.total = res.total;
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    getPaginationParam() {
      return {
        offset: (this.table.currentPage - 1) * this.table.pageSize,
        limit: this.table.pageSize
      };
    },

    /**
     * 当前页改变事件
     */
    pageChange(newPage) {
      this.table.currentPage = newPage;
      this.getAllReports();
    },
    /**
     * 每页显示条数改变事件
     */
    pageSizeChange(newPageSize) {
      this.table.pageSize = newPageSize;
      this.table.currentPage = 1;
      this.getAllReports();
    }
  }
};
</script>




