<template>
  <div class="tabBody">
    <gs-row>
      <gs-radio
        v-model="type"
        label="every"
      >每小时</gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        label="increment"
      >
        从第 <el-input-number
          v-model="start"
          :min="0"
          :max="23"
          size="mini"
        /> 小时开始，
        每隔 <el-input-number
          v-model="increment"
          :min="0"
          :max="23"
          size="mini"
        /> 小时执行
      </gs-radio>
    </gs-row>
    <gs-row>
      <gs-radio
        v-model="type"
        class="long"
        label="specific"
      >具体小时数(可多选)
        <el-select
          v-model="specific"
          size="mini"
          multiple
          popper-class="crontab-hour-select"
        >
          <el-option
            v-for="(val, index) in 24"
            :key="index"
            :value="val-1"
          >{{ val-1 }}</el-option>
        </el-select>
      </gs-radio>
    </gs-row>
  </div>
</template>
<script>
import crontabMixin from './mixins/cron.mixin.js';

export default {
  name: 'CrontabHour',
  mixins: [crontabMixin]
};
</script>



