<template>
  <div
    v-loading="isLoading"
    class="template-detail"
  >
    <el-table
      :data="detail"
      stripe
    >
      <el-table-column
        v-for="item in tableConfig"
        :key="item.name"
        :label="item.name"
        align="center"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          {{ scope.row[item.propertyName] }}
        </template>
      </el-table-column>
      <el-table-column
        label="启动时间"
        align="center"
        width="120"
        prop="startTime"
        show-overflow-tooltip
      />
      <el-table-column
        align="center"
        prop="user"
        label="操作"
        width="220"
      >
        <template slot-scope="scope">
          <action-comfirm
            :title="'确定' + (scope.row.templateStatus === 1 ? '暂停' : '启动') +'该模板吗？'"
            @comfirm="changeTemplateStatus(scope)"
          >
            <gs-button
              slot="reference"
              type="text-primary"
            >{{ scope.row.templateStatus === 1 ? '暂停' : '启动' }}</gs-button>
          </action-comfirm>
          <gs-button
            slot="reference"
            type="text-primary"
            @click="editTemplate(scope)"
          >编辑</gs-button>
          <gs-button
            slot="reference"
            type="text-primary"
            @click="copyTemplate(scope)"
          >克隆</gs-button>
          <gs-button
            slot="reference"
            type="text-primary"
            @click="viewTemplate(scope)"
          >查看</gs-button>
          <action-comfirm
            title="确定删除该模板吗？"
            @comfirm="deleteTemplate()"
          >
            <gs-button
              slot="reference"
              type="text-primary"
            >删除</gs-button>
          </action-comfirm>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import './template-detail.scss';
import ActionComfirm from 'src/components/action-comfirm';
import * as dataQualityService from 'src/service/metadata-management/data-quality';
export default {
  components: {
    ActionComfirm
  },
  props: {
    templateInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      isLoading: false,
      detail: [],
      tableConfig: [
        {
          name: '模板名称',
          propertyName: 'templateName'
        },
        {
          name: '生成周期',
          propertyName: 'buildTypeName'
        },
        {
          name: '表规则数量',
          propertyName: 'tableRulesNum'
        },
        {
          name: '字段规则数量',
          propertyName: 'columnRulesNum'
        },
        {
          name: '橙色警告数量',
          propertyName: 'orangeAlerts'
        },
        {
          name: '红色警告数量',
          propertyName: 'redAlerts'
        }
      ]
    };
  },

  // beforeMount() {
  //   this.getTemplateDetail();
  // },
  watch: {
    templateInfo: {
      handler: function (val) {
        this.getTemplateDetail();
      },
      immediate: true
    }
  },
  methods: {
    copyTemplate(scope) {
      this.$emit('copyTemplate', scope.row.buildType);
    },
    editTemplate(scope) {
      this.$emit('editTemplate', scope.row.buildType);
    },
    viewTemplate(scope) {
      this.$emit('viewTemplate', scope.row.buildType)
    },
    /**
     * 删除模板
     */
    deleteTemplate() {
      this.isLoading = true;
      dataQualityService.deleteTemplate(this.templateInfo.templateId).then(res => {
        this.isLoading = false;
        this.$emit('handleDelete');
        this.$Message.success('删除模板成功', 1.5);
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    /**
     * 修改模板状态
     */
    changeTemplateStatus() {
      this.isLoading = true;
      const status = this.templateInfo.templateStatus === 1 ? 2 : 1;
      dataQualityService.changeTemplateStatus(this.templateInfo.templateId, status).then(res => {
        this.isLoading = false;
        this.$Message.success(`${this.templateInfo.templateStatus === 1 ? '暂停' : '启动'}模板成功`, 1.5);
        this.$emit('handleChangeStatus');
      }).catch((res) => {
        this.isLoading = false;
      });
    },
    getTemplateDetail() {
      const buildTypeName = this.templateInfo.buildType === 0 ? '周期生成' : '一次性生成';
      this.detail = [{...this.templateInfo, buildTypeName}];
    }
  }
};
</script>




