<template>
  <div class="create-rule-table">
    <el-table
      :data="dataSources"
      border
    >
      <el-table-column
        prop="columnName"
        align="center"
        label="规则名称"
        width="180"
      >
        <template slot-scope="scope">
          <span v-if="isView">{{scope.row.selectRule.ruleName}}</span>
          <gs-select
           v-else
            v-model="scope.row.selectRule"
            :validate-event="false"
            @change="handleColumnChange(scope)"
          >
            <gs-option
              v-for="(item, index) in tableRules"
              :key="index"
              :label="item.ruleName"
              :value="item"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="description"
        show-overflow-tooltip
        label="说明"
      >
        <template slot-scope="scope">
          <span>
            {{ scope.row.selectRule.ruleInfo }}
          </span>
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="type"
        width="120"
        label="阈值校验类型"
      >
        <template slot-scope="scope">
          <span v-if="isView">{{scope.row.ruleCheckType === 0 ? '固定值' : '波动值'}}</span>
          <gs-select
            v-else
            v-model="scope.row.ruleCheckType"
            :validate-event="false"
            @change="handleCheckTypeChange(scope)"
          >
            <gs-option
              v-for="(item, index) in scope.row.selectRule.ruleCheckTypeOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </gs-select>
        </template>
      </el-table-column>
      <el-table-column
        label="校验表达式"
        align="center"
        width="120"
        prop="type"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <span v-if="isView">{{getExpression(scope)}}</span>
          <gs-select
            v-if="scope.row.ruleCheckType === 0 && !isView"
            v-model="scope.row.ruleCheckExpression"
            :validate-event="false"
          >
            <gs-option
              v-for="(item, index) in ruleCheckExpressionOptions"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </gs-select>
          <div v-else />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="description"
        label="阈值大小"
        width="260"
      >
        <template slot-scope="scope">
          <div class="rule-check-wrap">
            <div class="rule-check-item">
              <gs-input-number
                :disabled="isView"
                v-model="scope.row.ruleCheckThreshold[0]"
                :validate-event="false"
                :placeholder="scope.row.ruleCheckType === 1 ? '橙色阈值' : '红色阈值'"
                step="1"
              />
              <span class="threshold-unit">{{ scope.row.selectRule.ruleCheckThresholdUnit }}</span>
            </div>
            <div
              v-if="scope.row.ruleCheckType === 1"
              class="rule-check-item"
            >
              <gs-input-number
                :disabled="isView"
                v-model="scope.row.ruleCheckThreshold[1]"
                :validate-event="false"
                :placeholder="'红色阈值'"
                step="1"
              />
              <span class="threshold-unit">{{ scope.row.selectRule.ruleCheckThresholdUnit }}</span>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column
       v-if="!isView"
        align="center"
        prop="id"
        width="120"
        label="操作"
      >
        <template slot-scope="scope">
          <action-comfirm
            title="确定删除该字段吗？"
            @comfirm="deleteField(scope)"
          >
            <span
              slot="reference"
              class="remove-icon"
            >
              <gs-icon name="close" />删除
            </span>
          </action-comfirm>
        </template>
      </el-table-column>
    </el-table>

    <span
      v-if="!isView"
      class="add-icon"
      @click="addField"
    >
      <gs-icon name="plus-circle" /><span>添加</span>
    </span>
  </div>
</template>

<script>
import emitter from '@gs-ui/gs-ui/lib/_utils/mixins/emitter';
import ActionComfirm from 'src/components/action-comfirm';

export default {
  components: {
    ActionComfirm
  },

  mixins: [emitter],

  props: {
    value: {
      type: Array,
      default() {
        return [];
      }
    },
    tableRules: {
      type: Array,
      default() {
        return [];
      }
    },
    isView: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      dataSources: this.value,
      ruleCheckExpressionOptions: [
        {
          label: '大于',
          value: 2
        },
        {
          label: '小于',
          value: 4
        },
        {
          label: '等于',
          value: 0
        },
        {
          label: '不等于',
          value: 1
        },
        {
          label: '大于等于',
          value: 3
        },
        {
          label: '小于等于',
          value: 5
        }
      ]
    };
  },

  watch: {
    value(val) {
      this.dataSources = val;
    }
  },
  methods: {
    isValid(value) {
      const re = /^[a-zA-Z$_][a-zA-Z\d_]*$/;

      return value && re.test(value);
    },

    addField() {
      this.dataSources.push({
        selectRule: this.tableRules[0],
        ruleCheckType: this.tableRules[0].ruleCheckTypeOptions[0].value,
        ruleCheckExpression: 2,
        ruleCheckThreshold: [...this.tableRules[0].ruleCheckThreshold]
      });
      this.$emit('input', this.dataSources);
    },

    deleteField(scope) {
      this.dataSources.splice(scope.$index, 1);
      this.$emit('input', this.dataSources);
      this.dispatch('GsFormItem', 'gs.form.change', this.dataSources);
    },

    handleColumnChange(scope) {
      const {
        $index,
        row
      } = scope;
      this.$nextTick(() => {
        row.ruleCheckType = row.selectRule.ruleCheckTypeOptions[0].value;
        row.ruleCheckThreshold = [...row.selectRule.ruleCheckThreshold];

        this.dataSources[$index] = row;
        this.$emit('input', this.dataSources);
      });
    },
    handleCheckTypeChange(scope) {
      const {
        $index,
        row
      } = scope;
      row.ruleCheckThreshold = [];
      if (row.ruleCheckType === 1) {
        // 波动值
        this.$delete(row, 'ruleCheckExpression');
      } else {
        this.$set(row, 'ruleCheckExpression', 2);
      }
      this.dataSources[$index] = row;
    },
    getExpression(scope) {
      const expression = this.ruleCheckExpressionOptions.find(item => item.value === scope.row.ruleCheckExpression)
      return expression.label || '';
    }
  }
};
</script>




